var bngapp=angular.module('BApp',['ui-notification','ngAnimate','ngMaterial','bsLoadingOverlay'])
.controller('BCtrl', function($scope, $mdMedia, $rootScope, BIQThemeManager){
    $scope.$mdMedia = $mdMedia;
	BIQThemeManager.init();
	
    angular.element(document).ready(function(){
	
    });
})
.factory('BIQThemeDialog', function($mdDialog, $mdMedia, bsLoadingOverlayService, Notification){
    var ref = new BIQThemeDialog($mdDialog, $mdMedia, bsLoadingOverlayService, Notification);
    
    return ref;
})
.factory('BIQThemeManager', function(BIQThemeDialog){
    var ref = new BIQThemeManager;
    ref.dialog = BIQThemeDialog;
    return ref;
}).directive('biqTab', biqTab)
.directive( 'biqTabItem', biqTabItem )
.directive( 'BIQWidgetElementParser', BIQWidgetElementParser );